import { Box, useMediaQuery, TextField } from "@mui/material";
import {getIn} from "formik";


const AddressForm =({
    type,
    values,
    errors,
    touched,
    handleBlur,
    handleChange,
}) => {
    const isNonMobile = useMediaQuery("(min-width:600px)");

    // these functions allow for better code readability
    const formattedName = (field) => `${type}.${field}`;

    const formattedError = (field) => 
        Boolean(   //checks if the following are true or false. Both need to be true to show an error
            getIn(touched, formattedName(field)) &&    //getIn is a utility function included in Formik. You can use it to extract a deeply nested value from an object by using its path.
            getIn(errors, formattedName(field))
        );

    const formattedHelper = (field) => 
        getIn(touched, formattedName(field)) &&
        getIn(errors, formattedName(field));

    return(
        <Box
            display="grid"
            gap="15px"
            gridTemplateColumns="repeat(4, minmax(0, 1fr))"
            sx={{
                "& > div": { gridColumn: isNonMobile ? undefined : "span 4"}
            }}
        >
            <TextField
                fullWidth
                type="text"
                label="First Name"
                onBlur={handleBlur}
                onChange={handleChange}
                value={values.firstName}
                name={formattedName("firstName")}
                error={formattedError("firstName")}
                helperText={formattedHelper("firstName")}
                sx={{ gridColumn: "span 2"}}   // column is split into 4. This textfield has span of 2.
            />
            <TextField
                fullWidth
                type="text"
                label="Last Name"
                onBlur={handleBlur}
                onChange={handleChange}
                value={values.lastName}
                name={formattedName("lastName")}
                error={formattedError("lastName")}
                helperText={formattedHelper("lastName")}
                sx={{ gridColumn: "span 2"}}   
            />
            <TextField
                fullWidth
                type="text"
                label="Country"
                onBlur={handleBlur}
                onChange={handleChange}
                value={values.country}
                name={formattedName("country")}
                error={formattedError("country")}
                helperText={formattedHelper("country")}
                sx={{ gridColumn: "span 4"}}   
            />
            <TextField
                fullWidth
                type="text"
                label="Street Address"
                onBlur={handleBlur}
                onChange={handleChange}
                value={values.street1}
                name={formattedName("street1")}
                error={formattedError("street1")}
                helperText={formattedHelper("street1")}
                sx={{ gridColumn: "span 2"}}   
            />
            <TextField
                fullWidth
                type="text"
                label="Street Address 2 (optional)"
                onBlur={handleBlur}
                onChange={handleChange}
                value={values.street2}
                name={formattedName("street2")}
                error={formattedError("street2")}
                helperText={formattedHelper("street2")}
                sx={{ gridColumn: "span 2"}}   
            />
            <TextField
                fullWidth
                type="text"
                label="City"
                onBlur={handleBlur}
                onChange={handleChange}
                value={values.city}
                name={formattedName("city")}
                error={formattedError("city")}
                helperText={formattedHelper("city")}
                sx={{ gridColumn: "span 2"}}   
            />
            <TextField
                fullWidth
                type="text"
                label="County"
                onBlur={handleBlur}
                onChange={handleChange}
                value={values.county}
                name={formattedName("county")}
                error={formattedError("county")}
                helperText={formattedHelper("county")}
                sx={{ gridColumn: "1fr"}}   
            />
            <TextField
                fullWidth
                type="text"
                label="Postcode"
                onBlur={handleBlur}
                onChange={handleChange}
                value={values.postcode}
                name={formattedName("postcode")}
                error={formattedError("postcode")}
                helperText={formattedHelper("postcode")}
                sx={{ gridColumn: "1fr"}}   
            />

            

        </Box>
    )
}

export default AddressForm